import React, { useState, useEffect } from 'react';
import { GeoJsonFeature } from '../types/GeoJsonTypes';
import { 
  Paper, 
  Typography, 
  List, 
  ListItem, 
  ListItemText, 
  Divider,
  Box,
  Button,
  TextField,
  ToggleButton,
  ToggleButtonGroup,
  Stack,
  CircularProgress,
  Alert,
  Drawer,
  IconButton,
  useMediaQuery,
  useTheme
} from '@mui/material';
import { styled } from '@mui/material/styles';
import MyLocationIcon from '@mui/icons-material/MyLocation';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import DirectionsIcon from '@mui/icons-material/Directions';
import CloseIcon from '@mui/icons-material/Close';
import { PressureChart } from './PressureChart';
import { WeatherService } from '../services/weatherService';

interface SidePanelProps {
  selectedLake: GeoJsonFeature | null;
  open?: boolean;
  onClose?: () => void;
}

const StyledSidePanel = styled(Paper)(({ theme }) => ({
  width: 300,
  padding: theme.spacing(3),
  height: '100vh',
  overflow: 'auto',
  boxShadow: theme.shadows[3],
  zIndex: 1000,
  position: 'relative',
  display: 'flex',
  flexDirection: 'column'
}));

const ContentSection = styled(Box)({
  flex: '1 1 auto',
  overflow: 'auto'
});

const DirectionsSection = styled(Box)(({ theme }) => ({
  flex: '0 0 auto',
  marginTop: theme.spacing(2),
  paddingTop: theme.spacing(2),
  borderTop: `1px solid ${theme.palette.divider}`
}));

const SidePanel: React.FC<SidePanelProps> = ({ selectedLake, open = true, onClose }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [locationType, setLocationType] = useState<'current' | 'manual'>('current');
  const [manualAddress, setManualAddress] = useState('');
  const [isGettingLocation, setIsGettingLocation] = useState(false);
  const [locationError, setLocationError] = useState<string | null>(null);
  const [pressureData, setPressureData] = useState<any>(null);
  const [pressureLoading, setPressureLoading] = useState(false);
  const [pressureError, setPressureError] = useState<string | null>(null);
  
  useEffect(() => {
    if (selectedLake) {
      const [lng, lat] = selectedLake.geometry.coordinates;
      setPressureLoading(true);
      setPressureError(null);
      
      WeatherService.fetchPressureData(lat, lng)
        .then(data => {
          setPressureData(data);
          setPressureLoading(false);
        })
        .catch(error => {
          setPressureError('Kunde inte hmta vderdata');
          setPressureLoading(false);
        });
    }
  }, [selectedLake]);

  const renderCaughtSpecies = (species: string[] | string | undefined): string => {
    if (!species) return 'Inga rapporterade';
    if (Array.isArray(species)) return species.join(', ');
    return species;
  };
  
  const handleGetDirections = async () => {
    setLocationError(null);
    
    if (locationType === 'current') {
      setIsGettingLocation(true);
      
      if (!navigator.geolocation) {
        setLocationError('Din webblsare stder inte geolokalisering');
        setIsGettingLocation(false);
        return;
      }

      navigator.geolocation.getCurrentPosition(
        (position) => {
          setIsGettingLocation(false);
          const origin = `${position.coords.latitude},${position.coords.longitude}`;
          openGoogleMapsDirections(origin);
        },
        (error) => {
          setIsGettingLocation(false);
          switch (error.code) {
            case error.PERMISSION_DENIED:
              setLocationError('tkomst till plats nekad');
              break;
            case error.POSITION_UNAVAILABLE:
              setLocationError('Platsinformation r inte tillgnglig');
              break;
            case error.TIMEOUT:
              setLocationError('Timeout vid hmtning av plats');
              break;
            default:
              setLocationError('Ett oknt fel uppstod');
          }
        }
      );
    } else if (manualAddress.trim()) {
      openGoogleMapsDirections(encodeURIComponent(manualAddress.trim()));
    }
  };

  const openGoogleMapsDirections = (origin: string) => {
    if (!selectedLake) return;
    const [lng, lat] = selectedLake.geometry.coordinates;
    const destination = `${lat},${lng}`;
    const url = `https://www.google.com/maps/dir/${origin}/${destination}`;
    window.open(url, '_blank');
  };

  const content = (
    <>
      {isMobile && onClose && (
        <Box display="flex" justifyContent="flex-end" mb={1}>
          <IconButton onClick={onClose} size="small">
            <CloseIcon />
          </IconButton>
        </Box>
      )}
      
      {!selectedLake ? (
        <Box display="flex" justifyContent="center" alignItems="center" height="100%">
          <Typography variant="body1" color="text.secondary">
            Vlj en sj p kartan fr att se mer information
          </Typography>
        </Box>
      ) : (
        <>
          <ContentSection>
            <Typography variant="h5" component="h2" gutterBottom color="primary">
              {selectedLake.properties.name}
            </Typography>
            <Divider sx={{ mb: 2 }} />
            <List disablePadding>
              <ListItem sx={{ py: 1 }}>
                <ListItemText 
                  primary="Maxdjup" 
                  secondary={selectedLake.properties.maxDepth !== null ? `${selectedLake.properties.maxDepth} m` : 'Oknt'} 
                />
              </ListItem>
              <ListItem sx={{ py: 1 }}>
                <ListItemText 
                  primary="Area" 
                  secondary={selectedLake.properties.area !== null && selectedLake.properties.area !== undefined
                    ? `${selectedLake.properties.area.toLocaleString()} ha`
                    : 'Oknd'} 
                />
              </ListItem>
              <ListItem sx={{ py: 1 }}>
                <ListItemText 
                  primary="Ln" 
                  secondary={selectedLake.properties.county} 
                />
              </ListItem>
              <ListItem sx={{ py: 1 }}>
                <ListItemText 
                  primary="Fngade arter" 
                  secondary={renderCaughtSpecies(selectedLake.properties.catchedSpecies || selectedLake.properties.fngadeArter)} 
                />
              </ListItem>
              <ListItem sx={{ py: 1 }}>
                <ListItemText 
                  primary="Vanligaste art" 
                  secondary={selectedLake.properties.vanlArt
                    ? `${selectedLake.properties.vanlArt} (${selectedLake.properties.vanlArtWProc}%)`
                    : 'Oknd'} 
                />
              </ListItem>
              <ListItem sx={{ py: 1 }}>
                <ListItemText 
                  primary="Nst vanligaste art" 
                  secondary={selectedLake.properties.nstVanlArt
                    ? `${selectedLake.properties.nstVanlArt} (${selectedLake.properties.nstVanlArtWProc}%)`
                    : 'Oknd'} 
                />
              </ListItem>
              <ListItem sx={{ py: 1 }}>
                <ListItemText 
                  primary="Senaste fisker" 
                  secondary={selectedLake.properties.senasteFisker || 'Oknt'} 
                />
              </ListItem>
            </List>
            
            <Divider sx={{ my: 2 }} />
            
            <PressureChart 
              data={pressureData}
              loading={pressureLoading}
              error={pressureError}
            />
          </ContentSection>
          
          <DirectionsSection>
            <Typography variant="subtitle2" gutterBottom color="primary" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <DirectionsIcon fontSize="small" />
              Vgbeskrivning
            </Typography>
            
            <Stack spacing={2}>
              <ToggleButtonGroup
                value={locationType}
                exclusive
                onChange={(e, newValue) => newValue && setLocationType(newValue)}
                size="small"
                fullWidth
              >
                <ToggleButton value="current">
                  <MyLocationIcon sx={{ mr: 1, fontSize: 18 }} />
                  Min position
                </ToggleButton>
                <ToggleButton value="manual">
                  <LocationOnIcon sx={{ mr: 1, fontSize: 18 }} />
                  Ange adress
                </ToggleButton>
              </ToggleButtonGroup>
              
              {locationType === 'manual' && (
                <TextField
                  size="small"
                  placeholder="Skriv din adress..."
                  value={manualAddress}
                  onChange={(e) => setManualAddress(e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      handleGetDirections();
                    }
                  }}
                  fullWidth
                />
              )}
              
              {locationError && (
                <Alert severity="error" sx={{ py: 0.5 }}>
                  {locationError}
                </Alert>
              )}
              
              <Button
                variant="contained"
                color="primary"
                onClick={handleGetDirections}
                disabled={isGettingLocation || (locationType === 'manual' && !manualAddress.trim())}
                startIcon={isGettingLocation ? <CircularProgress size={16} /> : <DirectionsIcon />}
                fullWidth
              >
                {isGettingLocation ? 'Hmtar position...' : 'F vgbeskrivning'}
              </Button>
            </Stack>
          </DirectionsSection>
        </>
      )}
    </>
  );

  if (isMobile) {
    return (
      <Drawer
        anchor="left"
        open={open}
        onClose={onClose}
        PaperProps={{
          sx: {
            width: '85%',
            maxWidth: 400,
            height: '100vh',
            display: 'flex',
            flexDirection: 'column'
          }
        }}
      >
        <Box sx={{ padding: theme.spacing(2), height: '100%', display: 'flex', flexDirection: 'column' }}>
          {content}
        </Box>
      </Drawer>
    );
  }

  return (
    <StyledSidePanel>
      {content}
    </StyledSidePanel>
  );
};

export default SidePanel;
